import React from 'react'

export const Csmart = () => {
  return (
    <div className="portfolio-modal light">
      <div className="module picture" style={{backgroundImage: `url(${process.env.PUBLIC_URL}/images/portofolio/mosaic.jpg)`}}>
        <div className="wrapper">
          {/* <h3 className="title" style={{color: '#fff'}}>VMD Lab</h3> */}
          <div className="tags">
            <div className="tag">Web Development</div>
            <div className="tag">Angular</div>
          </div>
        </div>
      </div>
      <div className="module padded">
        <div className="heading">
          <div className="title">CSMART</div>
          <div className="desc">
            <span>2017 - 2018</span>
            <span>&nbsp;&nbsp;·&nbsp;&nbsp;</span>
            <span>Bank Central Asia</span>
          </div>
        </div>
        <p className="text">
          CSMART is a web application that functions as a Swift Message Investigation Tool. 
          The main function of the application is to investigate problematic Swift Message Case 
          and repair it through a specific set of processes depending on the type of case and the problem it encounters. 
          CSMART consume Spring Boot API as the main backend system. 
        </p>
        <p className="text">
          On the front-end side, CSMART Utilize Angular 6 and bootstrap 4 CSS framework as the main foundation.
          With a combination of RxJS and a modular nature of Angular 6, making this application excels in term of 
          speed performance, effectiveness, and accessibility.
        </p>
        
        <div className="actions center">
          <a href='https://github.com/tonyhazama/vmdlab' target='_blank' rel="noopener noreferrer" className="action">
            <i className="fab fa-github" title="View on Github"></i>
          </a>
          <a href='https://tonyhazama.github.io/vmdlab/' target='_blank' rel="noopener noreferrer" className="action">
            <i className="fas fa-globe-asia" title="Live Preview"></i>
          </a>
        </div>
      </div>
    </div>
  )
}
